import React from 'react';

const Home = () => {
  return (
    <div className="HomePage" id="home"> <div id="myCarousel" class="carousel slide mb-6" data-bs-ride="carousel">
        <div class="carousel-inner">
    
          <div id="Slide1" class="carousel-item active">
            <div class="container">
              <div class="carousel-caption text-start">
                <h1
                  className="display-5 fw-bold "
                  style={{ color: "white", textAlign: "center" }}
                >
                  సుందరకాండ పారాయణం
                </h1>
                
                <p
                  className="fw-bold"
                  style={{ color: "white", textAlign: "center" }}
                >
                  “పునాతి భవనం పుణ్యా రామాయణ మహానది”
                </p>
              </div>
            </div>
          </div>
          <div id="Slide2" class="carousel-item">
            <div class="container">
              <div class="carousel-caption">
                <strong>
                  {" "}
                  <h1
                    className="display-5 fw-bold "
                    style={{ color: "white", textAlign: "center" }}
                  >
                    ఓం ఉగ్రలోచనాయ నమః
                  </h1>
                </strong>
                <p
                  className="fw-bold"
                  style={{ color: "white", textAlign: "center" }}
                >
                  పశ్చిమే పాతు సర్వేశో దిశి మే సర్వతోముఖః | నృసింహః పాతు
                  వాయవ్యాం సౌమ్యాం భూషణవిగ్రహః || ౧౮ ||
                </p>
              </div>
            </div>
          </div>
          <div id="Slide3" class="carousel-item active">
            <div class="container">
              <div class="carousel-caption text-start">
                <h1
                  className="display-5 fw-bold "
                  style={{ color: "white", textAlign: "center" }}
                >
                  అనంత పద్మనాభ స్వామి
                </h1>
                <p
                  className="fw-bold"
                  style={{ color: "white", textAlign: "center" }}
                >
                  నమోఽస్త్వనంతాయ సహస్రమూర్తయే సహస్రపాదాక్షిశిరోరుబాహవే ।
                  సహస్రనామ్నే పురుషాయ శాశ్వతే సహస్రకోటీ యుగధారిణే నమః ॥
                </p>
              </div>
            </div>
          </div>
        </div>
        <button
          class="carousel-control-prev"
          type="button"
          data-bs-target="#myCarousel"
          data-bs-slide="prev"
        >
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Previous</span>
        </button>
        <button
          class="carousel-control-next"
          type="button"
          data-bs-target="#myCarousel"
          data-bs-slide="next"
        >
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Next</span>
        </button>
      </div>
      
    </div>
  );
};

export default Home;
